"use client";

import React, { useState } from "react";
import JobCard from "./JobCard";

interface JobApplication {
  id: number;
  title: string;
  company: string;
  status: "1st Round" | "2nd Round" | "Selected" | "Rejected";
  feedback?: string;
}

export default function MyJob() {
  const [applications] = useState<JobApplication[]>([
    {
      id: 1,
      title: "Frontend Developer",
      company: "TechNova",
      status: "1st Round",
    },
    {
      id: 2,
      title: "Backend Engineer",
      company: "CloudWorks",
      status: "Rejected",
      feedback: "Good technical skills, but looking for more backend experience.",
    },
    {
      id: 3,
      title: "UI/UX Designer",
      company: "Designify",
      status: "Selected",
    },
  ]);

  return (
    <div>
      <h1 className="text-2xl font-semibold text-gray-800 mb-6">My Jobs</h1>

      {applications.length === 0 ? (
        <p className="text-gray-600">You haven’t applied to any jobs yet.</p>
      ) : (
        <div className="grid md:grid-cols-2 gap-6">
          {applications.map((job) => (
            <JobCard key={job.id} job={job} />
          ))}
        </div>
      )}
    </div>
  );
}
